.catalog-video {
  text-transform: uppercase;
  padding: 0 20px;
  text-align: center;
  position: relative;

  img {
    @media (min-width: @tablet-width) {
      height: 500px;
      width: 100%;
    }
  }

  @media (min-width: @tablet-width) {
    padding: 0 60px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 200px;
  }
}

.catalog-video__title {
  margin: 0;
  font-size: 18px;
  line-height: 26px;

  @media (min-width: @tablet-width) {
    font-size: 30px;
  }
}

.catalog-video__description {
  margin: 0;
  padding-top: 29px;
  padding-bottom: 38px;
  padding-left: 10px;
  padding-right: 10px;

  @media (min-width: @tablet-width) {
    padding-left: 93px;
    padding-right: 93px;
    padding-bottom: 88px;

    br {
      display: none;
    }
  }

  @media (min-width: @desktop-width) {
    padding-left: 170px;
    padding-right: 170px;
    padding-top: 31px;
  }
}

.catalog-video__screenshot {
  vertical-align: middle;
}

.catalog-video__button {
  position: absolute;
  background-color: transparent;
  z-index: 1;
  width: 48px;
  height: 54px;
  top: 219px;
  left: 136px;
  background-image: url("../images/catalog/play-button.svg");
  cursor: pointer;
  border: none;

  @media (min-width: @tablet-width) {
    top: 408px;
    left: 360px;
  }

  @media (min-width: @desktop-width) {
    top: 410px;
    left: 573px;
  }
}
